import CardDisplayFront from "./CardDisplayFront";
import CardDisplayBack from "./CardDisplayBack";

//import Button from "react-bootstrap/Button";

function ShowCards({ cards, flipCard, setFlipCard }) {
  return (
    <div>
      <ul className="mb-5 ps-0">
        {cards.map((card) => (
          <li key={card.cardId} className="card-list-element">
            {!flipCard && <CardDisplayFront
              cardName={card.cardName}
              cardNumber={card.cardNumber}
              expMonth={card.expMonth}
              expYear={card.expYear}
              cvv={card.cvv}
              cardType={card.cardType}
              flipCard={flipCard}
              setFlipCard={setFlipCard}
              cardPosition="static"
            />}
            {flipCard && (
              <CardDisplayBack
                flipCard={flipCard}
                setFlipCard={setFlipCard}
                cardType={card.cardType}
                cvv={card.cvv}
                cardName={card.cardName}
                cardPosition="static"
              />
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default ShowCards;
